@import '../../utiles/vars';

.balance {
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	height: 44px;
	gap: 174px;
	margin: 40px 0 48px 0;

	&__counter {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row;
		width: 369px;
		height: 44px;
		.balance__inputs {
			display: flex;
			flex-direction: row;
			align-items: center;
		}
	}

	&__calculations {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row;
		width: 185px;
		height: 44px;
		font-size: 12px;

		.calculations_button {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: row;
			gap: 15px;
			height: 24px;
			width: 185px;

			&:hover {
				scale: 1.02;
			}
		}
	}
}

.span__balance {
	color: $main-text-color;
	padding-right: 21px;
}

.input-wrapper {
	position: relative;
	display: inline-block;
	width: 125px;
	height: 44px;
	padding-right: 15px;

	input {
		width: 100%;
		height: 100%;
		padding-right: 45px;
		padding-left: 12px;
		font-size: 16px;
		border: 3px solid #fff;
		border-radius: 12px;
		background-color: $main-color;
		box-sizing: border-box;

		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	.currency {
		color: $main-text-color;
		position: absolute;
		right: 22px;
		top: 50%;
		transform: translateY(-50%);
		font-weight: bold;
		pointer-events: none;
		font-size: 14px;
	}
}

.button {
	color: $main-text-color;
	font-size: 12px;
	text-transform: uppercase;
	width: 125px;
	height: 44px;
	border: 3px solid #fff;
	border-radius: 12px;
	background-color: $main-color;
	&:hover {
		transition: background 0.5s;
		transition: color 0.5s;
		background: $orange-color;
		color: #fff;
	}
}

